.ProductsMainDiv{
    display: flex;
    flex-direction: column;
    background-color: white;
    z-index: 1;
    margin-left: 5px;
    margin-right: 5px;
    /* box-shadow: 0px 0px 10px; */
    margin-top: 15px;
    width: 100%;
    min-width: 100px;
    justify-content: flex-end;
}

.productInfoDiv{
    padding-left: 10px;
}

.pTitle{
    margin-top: 5px;
}

.rattingDiv{
    display: flex;
    margin-bottom: 10px;
}

.pImage{
    width: 100%;
    max-height: 200px;
    object-fit: contain;
    margin-bottom: 15px;
}

.priceDiv{
    margin-top: 5px;
    margin-bottom: 5px;
}

.pButton{
    background-color: orange;
    border: 1px solid;
    padding: 5px;
    font-weight: bold;
    margin-bottom: 15px;
    margin-left: auto;
    margin-right: auto;
    cursor: pointer;
}