.headerLogo{
    width: 100px;
    margin-top: 18px;
    margin-left: 10px;
    margin-bottom: 5px;
    margin-right: 10px;
    object-fit: contain;
    
}

.header{
    z-index: 100;
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    background-color: rgb(0, 0, 82);
}

.searchIcon{
    height: 22px !important;
    padding: 5px;
    background-color: rgb(235, 200, 1);
}

.searchDiv{
    display: flex;
    flex: 1;

}

.headerNav{
    margin-right: 10px;
    margin-left: 10px;
    display: flex;
    justify-content: space-evenly;
}

.headerLink{
    color: white;
    text-decoration: none;
}

.headerOtions{
    display: flex;
    flex-direction: column;
}

.span1{
    font-size: 10px;
}

.span2{
    font-size: 13px;
    font-weight: bold;
}

.searchInput{
    width: 100%;
    height: 12px;
    border: none;
    padding: 10px;
}

.basketDiv{

    align-content: center;
    display: flex;
    color: white;
}

.counter{
    margin-left: 5px;
    margin-right: 10px;
}